<style>
div.qrcode-area {
  margin: 0 auto;
  text-align: center;
}

div.qrcode-area {
  width: 200px;
  height: 200px;
  position: relative;
}

#qrcode {
  width: 100%;
  height: 100%;
}

#character {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 80px;
  left: 80px;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

div.character-name {
  width: 100%;
  text-align: center;
}

img.character_icon {
  width: 40px;
  height: 40px;
}
</style>

<script>
import qrcode from 'qrcode'
import axios from 'axios'
import BigNumber from 'bignumber.js'
import bip21 from 'bip21'

const TICKER = {
  ID: 2688,
  URL: 'https://api.coinmarketcap.com/v2/ticker/'
}
const BIP21URN = 'vipstarcoin'

const vipsCoinIcon = require("./assets/coin.png")
const kanameIcon = require("./assets/kaname.png")
const irohaIcon = require("./assets/iroha.png")
const risaIcon = require("./assets/risa.png")
const hikariIcon = require("./assets/hikari.png")
const moyashiIcon = require("./assets/moyashi.png")
const terekoIcon = require("./assets/tereko.png")

export default {
  data () {
    return {
      address: 'VUmino3VbniKFyCzgYwpt8ZWfWumLGzSX4',
      character: 'coin',
      characterIconPath: vipsCoinIcon,
      qrcodeDataUri: '',
      amount: '0',
      fiat: '0',
      message: '',
      focused: '',
      fiatRate: 0
    }
  },
  mounted: function () {
    this.getFiat().then(price => {
      this.fiatRate = price
      this.updateQRCode()
    }).catch(error => {
      console.error(error)
    })
  },
  methods: {
    updateQRCode: function () {
      let opt = {}
      if (this.amount !== '0') opt.amount = this.amount
      if (this.message !== '') opt.message = this.message
      let uri = bip21.encode(this.address, opt, BIP21URN)
      return new Promise((resolve, reject) => {
        qrcode.toDataURL(
          uri,
          {
            errorCorrectionLevel: 'Q',
            type: 'image/png'
          },
          (err, uri) => {
            if (err !== null) {
              reject(err)
            } else {
              resolve(uri)
            }
          }
        )
      }).then(uri => {
        return new Promise((resolve, reject) => {
          this.qrcodeDataUri = uri
          resolve()
        })
      })
    },
    onAmount: function () {
      this.focused = 'amount'
    },
    onFiat: function () {
      this.focused = 'fiat'
    },
    getFiat: function () {
      let code = 'JPY'
      let uri = `${TICKER.URL}${TICKER.ID}/?convert=${code}`
      return new axios.get(uri).then(resp => {
        let quote = resp.data.data.quotes[code]
        if (quote === null) {
          return Promise.reject(new Error(`${code} does'nt supported`))
        } else {
          let price = new BigNumber(quote.price)
          return Promise.resolve(price)
        }
      }).catch(error => {
        return Promise.reject(error)
      })
    }
  },
  watch: {
    amount: function () {
      if (this.focused !== 'amount') {
        return
      }
      let amount = new BigNumber(this.amount)
      let fiat = amount.multipliedBy(this.fiatRate)
      if (fiat.toString() === 'NaN') {
        this.fiat = '0'
      } else {
        this.fiat = fiat.toString()
      }
      this.updateQRCode()
    },
    fiat: function () {
      if (this.focused !== 'fiat') {
        return
      }
      let fiat = new BigNumber(this.fiat)
      let amount = fiat.dividedBy(this.fiatRate)
      if (amount.toString() === 'NaN') {
        this.amount = '0'
      } else {
        this.amount = amount.decimalPlaces(8).toString()
      }
      this.updateQRCode()
    },
    message: function () {
      this.updateQRCode()
    },
    character: function () {
      switch (this.character) {
        case "coin":
          this.characterIconPath = vipsCoinIcon
          break
        case "kaname":
          this.characterIconPath = kanameIcon
          break
        case "iroha":
          this.characterIconPath = irohaIcon
          break
        case "risa":
          this.characterIconPath = risaIcon
          break
        case "hikari":
          this.characterIconPath = hikariIcon
          break
        case "moyashi":
          this.characterIconPath = moyashiIcon
          break
        case "tereko":
          this.characterIconPath = terekoIcon
          break
      }
    }
  }
}
</script>



